        </div>
        <el-row :gutter="20" style="padding-left: 80px; padding-right: 80px;">
          <el-col :span="3">
            <div class="columns_display" style="float:left">
              <!-- checkbox -->
              <el-popover
                placement="bottom"
                :width="200"
