            <el-button @click="exportToExcel" type="primary" plain style="width: 100px;">Download</el-button>
          </el-col>
        </el-row>
      </div>

      <div class="bg_table_3">
        <!-- @sort-changesortable="custom"
          https://blog.csdn.net/zebghfv/article/details/120993629 -->
        <el-table
          class="tableResults"
          :data="myResults.slice((currpage - 1) * pagesize, currpage * pagesize)"
          border
          style="margin: 10px 0; text-align: center; width: 100%; max-width: 2000px"
          :header-cell-style="{ background: '#ecf5ff', color: '#337ecc' }"
          v-loading="loading"
          @sort-change="handleSort"
          :row-class-name="rowClassName"
        >
          <!-- https://blog.csdn.net/weixin_45777909/article/details/108107729 -->
          <el-table-column label="Selector" min-width="100px" align="center">
            <template #default="scope">
              <el-checkbox @change="selectRow(scope.row, $event)" v-model="scope.row.check" />
            </template>
          </el-table-column>
          <el-table-column v-if="checkedColumns.includes('Methylation')" prop="probeID" label="Methylation" min-width="120px" align="center" />
          <!-- <el-table-column v-if="checkedColumns.includes('Methylation position')" prop="methylation_position" label="Methylation position" min-width="120px" align="center"/> -->
          <el-table-column v-if="checkedColumns.includes('Methylation position')" label="Methylation position" min-width="120px" align="center">
            <template v-slot="slotProps">
              {{ slotProps.row.ProbeChr }}:{{ slotProps.row.Probe_bp }}
            </template>
          </el-table-column>
          <el-table-column v-if="checkedColumns.includes('Cancer')" prop="cancer" label="Cancer" min-width="120px" align="center" />
          <!-- b_SMR   se_SMR  p_SMR p_SMR_multi     p_HEIDI nsnp_HEIDI      p_thresh        fdr -->
          <el-table-column v-if="checkedColumns.includes('SMR_beta')" prop="b_SMR" label="SMR_beta" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('SMR_SE')" prop="se_SMR" label="SMR_SE" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('SMR_P')" prop="p_SMR" label="SMR_P" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('SMR_P_multi')" prop="p_SMR_multi" label="SMR_P_multi" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('SMR_FDR')" prop="fdr" label="SMR_FDR" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('HEIDI_P')" prop="p_HEIDI" label="HEIDI_P" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('HEIDI_nsnp')" prop="nsnp_HEIDI" label="HEIDI_nsnp" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('Gene')" prop="Gene" label="Gene" min-width="120px" align="center"/>
        
          <!-- topSNP  topSNP_chr      topSNP_bp       A1      A2      Freq -->
          <el-table-column v-if="checkedColumns.includes('Top SNP')" prop="topSNP" label="Top SNP" min-width="120px" align="center">
            <template v-slot="scope">
              <a :href="'http://localhost:8081/cancerdb/search_pleio_snp?snp=' + scope.row.topSNP" target="_blank" class="link-snpid">
                {{ scope.row.topSNP }}
              </a>
            </template>
          </el-table-column>
          <el-table-column v-if="checkedColumns.includes('SNP position')" prop="SNP_position" label="SNP position" min-width="120px" align="center"/>
          <el-table-column v-if="checkedColumns.includes('EA/OA')" prop="alleles" align="center"  width="120 px">
            <template v-slot:header>
            <span>EA/OA</span>
            <el-tooltip
                content="Effect allele/Other allele"
                placement="top-start"
                effect="light"
            >
                <el-icon class="icon">
                    <QuestionFilled />
                </el-icon>
            </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column v-if="checkedColumns.includes('EAF')" prop="Freq" min-width="120px" align="center">
            <template v-slot:header>
            <span>EAF</span>
            <el-tooltip
                content="Effect allele frequency"
                placement="top-start"
                effect="light"
            >
                <el-icon class="icon">
                    <QuestionFilled />
                </el-icon>
            </el-tooltip>
            </template>
          </el-table-column>

          <!-- b_meQTL  se_meQTL p_meQTL -->
          <el-table-column v-if="checkedColumns.includes('meQTL_beta')" prop="b_eQTL" label="meQTL_beta" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('meQTL_SE')" prop="se_eQTL" label="meQTL_SE" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('meQTL_P')" prop="p_eQTL" label="meQTL_P" min-width="120px" align="center" sortable="custom"/>

          <!-- b_GWAS  se_GWAS p_GWAS -->
          <el-table-column v-if="checkedColumns.includes('GWAS_beta')" prop="b_GWAS" label="GWAS_beta" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('GWAS_SE')" prop="se_GWAS" label="GWAS_SE" min-width="120px" align="center" sortable="custom"/>
          <el-table-column v-if="checkedColumns.includes('GWAS_P')" prop="p_GWAS" label="GWAS_P" min-width="120px" align="center" sortable="custom"/>
          
        </el-table>
      </div>


  <!-- SMR -->

      <div class="fig_description">
        <span style="font-size: 20px; font-weight: bold; color: white;">
          SMR/meQTL/GWAS figures in methylation and its  1Mb flanking region based on the selected methylation 
          <span :style="{color: selectedMethy ? '#ffd04b' : 'white'}">{{ selectedMethy }}</span> 
          and cancer 
          <span :style="{color: selectedCancer ? '#ffd04b' : 'white'}">{{ selectedCancer }}</span>
        </span>
      </div>
      <div class="plot" v-loading="loading_echart">
        <div id="chart_meQTL" ref="chart_meQTL" style="width: 100%; height: 800px;"></div>
        <div id="chart_zscore" ref="chart_zscore" style="width: 100%; height: 800px;"></div>
      </div>
      

</template>

<script>
import { Search,RefreshRight } from "@element-plus/icons-vue";
import axios from "axios";
//nprogress
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import * as echarts from "echarts";
import { markRaw } from "vue";

//axios
//request NProgress.start()
axios.interceptors.request.use(config =>{
  console.log(config);
  NProgress.start();
  config.headers.Authorization = window.sessionStorage.getItem('token');
  return config;//config
})
//response NProgress.done()
axios.interceptors.response.use(config =>{
  console.log(config);
  NProgress.done();
  return config;//config
})

const columnOptions = ['Methylation', "Methylation position", 'Cancer',"SMR_beta","SMR_SE","SMR_P","SMR_P_multi","SMR_FDR","HEIDI_P","HEIDI_nsnp","Gene", 'Top SNP', 'SNP position', "EA/OA", "EAF", "meQTL_beta","meQTL_SE","meQTL_P","GWAS_beta","GWAS_SE","GWAS_P"];

export default {
  name: "meQTL_SMR",
  data() {
    const cancerTypes = ['Basal cell carcinoma', 'Breast cancer', 'Kidney cancer, except renal pelvis', 'Cervical cancer', 'Colorectal cancer',
     'Endometrial cancer', 'Esophageal cancer', 'Head and neck cancer', 'Lymphocytic leukemia', 'Lung cancer', 'Ovarian cancer', 
     'Pancreatic cancer', 'Prostate cancer', 'Skin melanoma', 'Stomach cancer', 'Squamous cell carcinoma', 'Thyroid cancer'];

    const options = cancerTypes.map(type => ({
      value: type,
      label: type
    }));

    return {
      Search,
      RefreshRight,

      // cancertype 
      options,
      cancerType: "", // 
      searchRegion: "", 
      searchMethy: "", // methy

      // 
      pagesize: 10,
      currpage: 1,
      // allresults: [],
      myResults: [], // 
      loading: false,

      // checkbox
      checkAll: true,
      columns: columnOptions,
      checkedColumns: columnOptions,
      isIndeterminate: false,

      currentSelectedRow: null, // 
      selectedMethy: '', // 
      selectedCancer: '', // 

      plot_data:[],
      chart_meQTL:null,
      chart_zscore:null,
      chart_GWAS:null,
      loading_echart:false,
      chart_heatmap:null,

      query:false,
    };
  },

  mounted() {
    this.query = this.$route.query.query;
    this.cancerType = [this.$route.query.cancer];
    if(this.query){
      this.queryData();
    } else {
      this.cancerType = "",
      // loading
      this.loading = true,
      axios.get("/api/cancerdb/SMR_meQTL").then((response) => {
        // this.allSNPs = response.data;
        this.myResults = response.data;
        this.loading = false;
        this.$nextTick(() => {
          this.plot_heatmap(this.myResults);
        });

        // 
        if (this.myResults.length > 0) {
          this.selectRow(this.myResults[0], true);
        }
      });
    }
  },

  methods: {
    handleSelectChange() {
      this.$nextTick(() => {
        // 
        const input = this.$refs.select && this.$refs.select.$el.querySelector('input');
        
        if (input) {
          input.value = ''; // 
        } else {
          console.error('Input element not found');
        }
      });
    },
    searchregion_eg() {
      // searchSNPs
      const buttonText = event.target.textContent.trim();
      this.searchRegion = buttonText; // 
      this.searchMethy = "";
    },
    searchmethy_eg() {
      // searchSNPs
      const buttonText = event.target.textContent.trim();
      this.searchMethy = buttonText; // 
      this.searchRegion = "";
    },
    handleCurrentChange(cpage) {
      this.currpage = cpage;
    },
    handleSizeChange(psize) {
      this.pagesize = psize;
    },
    // 
    handleSort(column) {
      if (column.order === "descending") {
        this.myResults.sort((a, b) => b[column.prop] - (a[column.prop]))
      } else if (column.order === "ascending") {
        this.myResults.sort((a, b) => a[column.prop] - (b[column.prop]))
      }
    },
    // 
    rounding(row, column, cellValue) {
      return cellValue.toFixed(3);
    },
    toggleDialog() {
      this.showDialog = !this.showDialog;
    },

    // checkbox
    handleCheckAllChange(val) {
      this.checkedColumns = val ? columnOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedColumnChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.columns.length;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.columns.length;
    },
    selectRow(row, value) {
      // 
      if (this.currentSelectedRow === row) {
        this.currentSelectedRow = null;
        row.checked = false;
      } else {
        this.myResults.forEach(item => {
          // 
          item.check = false;
        });
        // 
        this.currentSelectedRow = row;
        row.check = value;
        this.selectedMethy = row.probeID;  // 
        this.selectedCancer = row.cancer; // 
        this.sendFigData(); // 
      }
    },
    rowClassName({ row }) {
      // 
      return row.check ? 'row-selected' : '';
    },

    // ExcelCSV
    exportToExcel() {
      // 1. proplabel
      const columnMap = {
        Methylation: 'probeID',
        'Methylation position': (row) => `${row.ProbeChr}:${row.Probe_bp}`,
        Cancer: 'cancer',

        SMR_beta: 'b_SMR',
        SMR_SE: 'se_SMR',
        SMR_P: 'p_SMR',
        SMR_P_multi: 'p_SMR_multi',
        SMR_FDR: 'fdr',
        HEIDI_P: 'p_HEIDI',
        HEIDI_nsnp: 'nsnp_HEIDI',
        Gene: 'Gene',

        "Top SNP": 'topSNP',
        "SNP position": 'SNP_position',
        "EA/OA": 'alleles',
        EAF: 'Freq',

        meQTL_beta: 'b_eQTL',
        meQTL_SE: 'se_eQTL',
        meQTL_P: 'p_eQTL',
        GWAS_beta: 'b_GWAS',
        GWAS_SE: 'se_GWAS',
        GWAS_P: 'p_GWAS'
      };
      // const out_table = this.myResults.slice((this.currpage - 1) * this.pagesize, this.currpage * this.pagesize)
      const out_table = this.myResults;
      // 2. label
      const headers = this.checkedColumns.map(prop => columnMap[prop]);
      
      // 3. checkedColumns
      const data = out_table.map(row => {
        return headers.map(prop => {
          // prop'Gene position'
          if (typeof prop === 'function') {
            return prop(row);
          }
          return row[prop];
        });
      });

      // 4. CSVExcel
      let csvContent = '';
      csvContent += this.checkedColumns.join(',') + '\n'; // 
      data.forEach(row => {
        csvContent += row.join(',') + '\n'; // 
      });

      // 5. blob
      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.setAttribute('href', url);
      link.setAttribute('download', 'SMR_meQTL_FDR0.05.csv');
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },


    // SMR 
    sendFigData() {
      this.loading_echart = true;
      axios.post("/api/cancerdb/SMR_meQTL_fig", {methy: this.selectedMethy, cancer: this.selectedCancer})
        .then(response => {
          this.plot_data = response.data;
          //  Chart.js 
          this.$nextTick(() => {
            this.plotManhattanWithChartJS(this.plot_data);
            // this.plotManhattanWithChartJS(this.plot_data);
            // Z
            this.plotZscore(this.plot_data);
            this.loading_echart = false;
          });
        })
        .catch(error => {
          console.error('Error sending data to the backend:', error);
        });
    },
    
    plotManhattanWithChartJS(input_data) {
      this.chart_meQTL = markRaw(echarts.init(this.$refs.chart_meQTL,null, { renderer : 'svg' })); // svg

      //  -log10(P)
      const meQTLData_sig = input_data.meQTL_result_sig.map(item => {
        return [item.BP, item.log10P];
      });
      const meQTLData_notsig = input_data.meQTL_result_notsig.map(item => {
        return [item.BP, item.log10P];
      });
      const GWASData_sig = input_data.GWAS_result_sig.map(item => {
        return [item.bp, item.log10P];
      });
      const GWASData_notsig = input_data.GWAS_result_notsig.map(item => {
        return [item.bp, item.log10P];
      });
      const SMRData_sig = input_data.SMR_result_sig.map(item => {
        return [item.Probe_bp, item.log10FDR];
      });
      const SMRData_notsig = input_data.SMR_result_notsig.map(item => {
        return [item.Probe_bp, item.log10FDR];
      });

      const xAxisConfig = {
        type: "value",
        min: input_data.methy_start,
        max: input_data.methy_end,
        splitNumber: 10,
        splitLine: { show: false }, // 
        axisLabel: {
          show: true,
          formatter: (value) => {
            // 
            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
          },
        },
      };
      const yAxisConfig = {
        type: "value", // 
        min: 0, // 
        splitNumber: 10,
        splitLine: { show: false }, // 
        minInterval: 1,
      };

      const threshSMR = {
        name: 'threshSMR',
        type: 'line',
        markLine: {
          symbol: 'none',
          data: [{ yAxis: 1.30103 }],
          label: { show: true, position: 'end', formatter: '-log10(0.05)' },
          lineStyle: { type: 'dashed', color: 'red'
          }
        },
      };
      const threshQTLGWAS = {
        name: 'threshQTLGWAS',
        type: 'line',
        markLine: {
          symbol: 'none',
          data: [{ yAxis: 7.30103 }],
          label: { show: true, position: 'end', formatter: '-log10(5e-8)' },
          lineStyle: { type: 'dashed', color: 'red' }
        },
      };

      const option = {
        tooltip: {
          trigger: 'item',
          formatter: function(params) {
            const seriesIndex = params.seriesIndex;
            let tooltipContent = '';

            if (seriesIndex === 0) {
              const data_tooltip = input_data.SMR_result_sig[params.dataIndex];
              tooltipContent = `<b>Methylation:</b> ${data_tooltip.probeID}<br/>
                                <b>Methylation position:</b> ${data_tooltip.ProbeChr}:${data_tooltip.Probe_bp}<br/>
                                <b>Gene:</b> ${data_tooltip.Gene}<br/>
                                <b>Beta:</b> ${data_tooltip.b_SMR}<br/>
                                <b>SE:</b> ${data_tooltip.se_SMR}<br/>
                                <b>P:</b> ${data_tooltip.p_SMR}<br/>
                                <b>P_multi:</b> ${data_tooltip.p_SMR_multi}<br/>                                
                                <b>FDR:</b> ${data_tooltip.fdr}<br/>
                                <b>HEIDI_P:</b> ${data_tooltip.p_HEIDI}<br/>
                                <b>HEIDI_nsnp:</b> ${data_tooltip.nsnp_HEIDI}<br/>`;
            } else if ( seriesIndex === 1) {
              const data_tooltip = input_data.SMR_result_notsig[params.dataIndex];
              tooltipContent = `<b>Methylation:</b> ${data_tooltip.probeID}<br/>
                                <b>Methylation position:</b> ${data_tooltip.ProbeChr}:${data_tooltip.Probe_bp}<br/>
                                <b>Gene:</b> ${data_tooltip.Gene}<br/>
                                <b>Beta:</b> ${data_tooltip.b_SMR}<br/>
                                <b>SE:</b> ${data_tooltip.se_SMR}<br/>
                                <b>P:</b> ${data_tooltip.p_SMR}<br/>
                                <b>P_multi:</b> ${data_tooltip.p_SMR_multi}<br/>                                
                                <b>FDR:</b> ${data_tooltip.fdr}<br/>
                                <b>HEIDI_P:</b> ${data_tooltip.p_HEIDI}<br/>
                                <b>HEIDI_nsnp:</b> ${data_tooltip.nsnp_HEIDI}<br/>`;
            } else if (seriesIndex === 2) {
              const data_tooltip = input_data.meQTL_result_sig[params.dataIndex];
              tooltipContent = `<b>SNP:</b> ${data_tooltip.SNP}<br/>
                                <b>SNP position:</b> ${data_tooltip.Chr}:${data_tooltip.BP}<br/>
                                <b>EA/OA:</b> ${data_tooltip.A1}/${data_tooltip.A2}<br/>
                                <b>EAF:</b> ${data_tooltip.Freq}<br/>
                                <b>Beta:</b> ${data_tooltip.b}<br/>
                                <b>SE:</b> ${data_tooltip.SE}<br/>
                                <b>P:</b> ${data_tooltip.p}`;
            } else if (seriesIndex === 3) {
              const data_tooltip = input_data.meQTL_result_notsig[params.dataIndex];
              tooltipContent = `<b>SNP:</b> ${data_tooltip.SNP}<br/>
                                <b>SNP position:</b> ${data_tooltip.Chr}:${data_tooltip.BP}<br/>
                                <b>EA/OA:</b> ${data_tooltip.A1}/${data_tooltip.A2}<br/>
                                <b>EAF:</b> ${data_tooltip.Freq}<br/>
                                <b>Beta:</b> ${data_tooltip.b}<br/>
                                <b>SE:</b> ${data_tooltip.SE}<br/>
                                <b>P:</b> ${data_tooltip.p}`;
            } else if (seriesIndex === 4) {
              const data_tooltip = input_data.GWAS_result_sig[params.dataIndex];
              tooltipContent = `<b>SNP:</b> ${data_tooltip.snpid}<br/>
                                <b>SNP position:</b> ${data_tooltip.hg19chr}:${data_tooltip.bp}<br/>
                                <b>EA/OA:</b> ${data_tooltip.a1}/${data_tooltip.a2}<br/>
                                <b>EAF:</b> ${data_tooltip.EURaf}<br/>
                                <b>Beta:</b> ${data_tooltip.beta}<br/>
                                <b>SE:</b> ${data_tooltip.se}<br/>
                                <b>P:</b> ${data_tooltip.pval}`;
            } else if (seriesIndex === 5) {
              const data_tooltip = input_data.GWAS_result_notsig[params.dataIndex];
              tooltipContent = `<b>SNP:</b> ${data_tooltip.snpid}<br/>
                                <b>SNP position:</b> ${data_tooltip.hg19chr}:${data_tooltip.bp}<br/>
                                <b>EA/OA:</b> ${data_tooltip.a1}/${data_tooltip.a2}<br/>
                                <b>EAF:</b> ${data_tooltip.EURaf}<br/>
                                <b>Beta:</b> ${data_tooltip.beta}<br/>
                                <b>SE:</b> ${data_tooltip.se}<br/>
                                <b>P:</b> ${data_tooltip.pval}`;
            }

            return tooltipContent;
          },
        },
        // toolbox: {
        //   feature: {
        //     dataZoom: {
        //       yAxisIndex: 'none' // y
        //     },
        //     restore: {},
        //     saveAsImage: {}
        //   }
        // },
        dataZoom: [
          {
            show: true,
            realtime: true,
            // start: 30, // 
            // end: 70,
            xAxisIndex: [0, 1, 2],
            top:'90%',
          },
          {
            type: 'inside',
            realtime: true,
            // start: 30,
            // end: 70,
            xAxisIndex: [0, 1, 2],
            top:'90%',
          }
        ],
        grid: [
          { height: '20%' },
          { top: '35%', height: '20%' },
          { top: '65%', height: '20%' }
        ],
        xAxis: [
          { ...xAxisConfig, gridIndex: 0, },
          { ...xAxisConfig, gridIndex: 1, },
          { ...xAxisConfig, gridIndex: 2, }
        ],
        
        yAxis: [
          { ...yAxisConfig, name: "-log10(SMR_FDR)", gridIndex: 0 },
          { ...yAxisConfig, name: "-log10(meQTL_P)", gridIndex: 1 },
          { ...yAxisConfig, name: "-log10(GWAS_P)", gridIndex: 2 },
        ],
        series: [
          {
            name: "Significant",
            type: "scatter",
            data: SMRData_sig,
            symbolSize: 5,
            itemStyle: {
                color: 'red'
            },
          },
          {
            name: "Insignificant",
            type: "scatter",
            data: SMRData_notsig,
            symbolSize: 5,
            itemStyle: {
                color: '#79bbff'
            },
          },
          {
            name: "Significant",
            type: "scatter",
            data: meQTLData_sig,
            xAxisIndex: 1,
            yAxisIndex: 1,
            symbolSize: 5,
            itemStyle: {
                color: 'red'
            },
          },
          {
            name: "Insignificant",
            type: "scatter",
            data: meQTLData_notsig,
            xAxisIndex: 1,
            yAxisIndex: 1,
            symbolSize: 5,
            itemStyle: {
                color: '#79bbff'
            },
          },
          {
            name: "Significant",
            type: "scatter",
            data: GWASData_sig,
            xAxisIndex: 2,
            yAxisIndex: 2,
            symbolSize: 5,
            itemStyle: {
                color: 'red'
            },
          },
          {
            name: "Insignificant",
            type: "scatter",
            data: GWASData_notsig,
            xAxisIndex: 2,
            yAxisIndex: 2,
            symbolSize: 5,
            itemStyle: {
                color: '#79bbff'
            },
          },
          // Threshold line for SMR (y = 1)
          {
            ...threshSMR,
            xAxisIndex: 0, // First grid (SMR)
            yAxisIndex: 0,
          },
          // Threshold line for eQTL and GWAS (y = 7.30103)
          {
            ...threshQTLGWAS,
            xAxisIndex: 1, // Second grid (eQTL)
            yAxisIndex: 1,
          },
          {
            ...threshQTLGWAS,
            xAxisIndex: 2, // Third grid (GWAS)
            yAxisIndex: 2,
          }
        ],
        legend: {
          data: [
            {
              name: "Significant",
              icon: 'circle', // 
              itemStyle: { color: 'red' } // 
            },
            {
              name: "Insignificant",
              icon: 'circle', // 
              itemStyle: { color: '#79bbff' } // 
            },
          ], // 
        }
      };

      // 
      // First initialization of the chart
      this.chart_meQTL.setOption(option);
    },

    plotZscore(input_data){
      this.chart_zscore = echarts.init(this.$refs.chart_zscore);

      //  qtl_zscore  gwas_zscore
      const scatterData = input_data.merged_result.map(item => [item.qtl_zscore, item.gwas_zscore]);

      // ECharts 
      const option = {
        title: {
          text: 'meQTL vs GWAS Z-scores',
          left: 'center'
        },
        tooltip: [
          {
            trigger: 'item',
            formatter: function(params) {
              const data_tooltip = input_data.merged_result[params.dataIndex]; // 
              return `
                    <b>SNP:</b> ${data_tooltip.SNP}<br>
                    <b>SNP position:</b> ${data_tooltip.Chr}:${data_tooltip.BP}<br>
                    <b>EA/OA:</b> ${data_tooltip.A1}/${data_tooltip.A2}<br/>
                    <table style="width: 100%; text-align: left; font-size: 12px;">
                      <tr>
                        <td></td>
                        <td><b>QTL</b></td>
                        <td><b>GWAS</b></td>
                      </tr>
                      <tr>
                        <td><b>EAF:</b></td>
                        <td>${data_tooltip.qtl_EAF}</td>
                        <td>${data_tooltip.gwas_EAF}</td>
                      </tr>
                      <tr>
                        <td><b>Beta:</b></td>
                        <td>${data_tooltip.qtl_b}</td>
                        <td>${data_tooltip.gwas_b}</td>
                      </tr>
                      <tr>
                        <td><b>SE:</b></td>
                        <td>${data_tooltip.qtl_se}</td>
                        <td>${data_tooltip.gwas_se}</td>
                      </tr>
                      <tr>
                        <td><b>P:</b></td>
                        <td>${data_tooltip.qtl_p}</td>
                        <td>${data_tooltip.gwas_p}</td>
                      </tr>
                    </table>
                `;
            },
          },
        ],
        xAxis: {
          name: 'meQTL Z-score',
          type: 'value'
        },
        yAxis: {
          name: 'GWAS Z-score',
          type: 'value'
        },
        series: [
          {
            type: 'scatter',
            data: scatterData,
            symbolSize: 10,
            itemStyle: {
              color: '#5470C6'
            }
          }
        ]
      };

      // 
      this.chart_zscore.setOption(option);

    },

    queryData() {
      this.currpage = 1;
      // 
      // 
      this.loading = true
      axios.post("/api/cancerdb/SMR_meQTL",{cancerType:this.cancerType,searchRegion:this.searchRegion,searchMethy:this.searchMethy})
        .then((res) => {
          if (res.data.length === 0) {
            // 0
            this.$alert("Sorry, the input content is not existed or not significant in our results.", "No results", {
              confirmButtonText: "OK",
            });
          } else if (res.data === "errorID") {
            // "errorID"
            this.$alert("Invalid genomic region search!", "Error", {
              confirmButtonText: "OK",
            });
          } else {
            // 
            this.myResults = res.data;
            this.plot_heatmap(this.myResults);
            this.selectRow(this.myResults[0], true);
          }
          this.loading = false;
          
        })
        .catch((err) => {console.log(err)})
    },

    clearData() {
      this.cancerType = "";
      this.searchRegion = "";
      this.searchMethy = "";
    },

    plot_heatmap(data) {
      // cancer1cancer2
      const methy = [...new Set(data.map(item => item.probeID))];
      const cancers = [...new Set(data.map(item => item.cancer))];

      // 
      const matrix = [];
      const dataMap = data.reduce((acc, item) => {
        const key = `${item.probeID}-${item.cancer}`;
        acc[key] = item.b_SMR;
        return acc;
      }, {});
      
      methy.forEach(probeID => {
        const row = [];
        cancers.forEach(cancer => {
          const value = dataMap[`${probeID}-${cancer}`] || null;
          row.push(value);
        });
        matrix.push(row);
      });


      // https://blog.csdn.net/weixin_50508597/article/details/123552563
      this.chart_heatmap = markRaw(echarts.init(this.$refs.chart_heatmap,null, { renderer : 'svg' })); // svg
      const maxValue = Math.max(...matrix.flat());
      const option = {
        title: {
          text: "Causal effect (beta) between methylation (exposure) and cancer (outcome) based on SMR analysis",
          left: 'center',  // left, right, center
          top: 0,  // 
          textStyle: {
            fontSize: 14,  // 
            fontWeight: 'bold',  // 
          },
        },
        textStyle: {
          fontSize: 12,  // 
        },
        tooltip: {
          position: 'bottom',
          formatter: function (params) {
            return `<b>Methylation:</b> ${methy[params.data[0]]}<br/>
                    <b>Cancer:</b> ${cancers[params.data[1]]}<br/>
                    <b>SMR_beta:</b> ${params.data[2]}<br/>`;  // params.data[2] heatmap
          }
        },
        grid: {
          left: '15%',
          right: '10%',
          bottom: '10%',
          top: '10%',
          height: '70%'
        },
        dataZoom: [
          {
            show: true,
            realtime: true,
            start: 0, // 
            end: 20,
            top:'90%',
          },
          {
            type: 'inside',
            realtime: true,
            start: 0,
            end: 20,
            top:'90%',
          }
        ],
        xAxis: {
          type: 'category',
          data: methy,
          name: 'Methylation',
          // nameGap: 5,
          splitArea: {
            show: false, // 
          }
        },
        yAxis: {
          type: 'category',
          data: cancers,
          name: 'Cancer',
          // nameGap: 5,
          splitArea: {
            show: false,
          }
        },
        visualMap: {
          min: -maxValue,
          max: maxValue,
          calculable: true,
          orient: 'vertical',
          left: '1250',
          top: '10%',
          inRange: {
            color: ['#313695','#FFFFFF','#a50026']
          }
        },
        series: [{
          name: 'SMR_beta',
          type: 'heatmap',
          data: [],
          label: {
            show: false,
          },
          itemStyle: {
            normal: {
              borderColor: '#fff',
              borderWidth: 1
            }
          }
        }]
      };

      // 
      const seriesData = [];
      for (let i = 0; i < matrix.length; i++) {
        for (let j = 0; j < matrix[i].length; j++) {
          seriesData.push([i, j, matrix[i][j]]);
        }
      }

      option.series[0].data = seriesData;
      this.chart_heatmap.setOption(option)
    },
  },
}
</script>
  
